@import '../variables';

h1, h2, h3, h4, h5, h6 {
  font-family: inherit;
  font-style: inherit;
  margin: @space-lg 0;
  font-weight: @heading-font-weight;
  line-height: @heading-line-height;
}

h1 {
  font-size: @fs-h1;
  clear: both;
}

h2 {
  font-size: @fs-h2;
  clear: both;
}

h3 {
  font-size: @fs-h3;
}

h4 {
  font-size: @fs-h4;
}

h5 {
  font-size: @fs-h5;
}

h6 {
  font-size: @fs-h6;
}