.hljs-attribute, 
.hljs-doctag, 
.hljs-keyword, 
.hljs-meta-keyword, 
.hljs-name, 
.hljs-selector-tag {
    font-weight: normal;
}

.hljs {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
}

.hljs,
.hljs-subst {
  color: var(--code-hl-default);
}

.hljs-comment {
  color: var(--code-hl-comment-color);
  background: var(--code-hl-comment-bg-color);
  padding: 1px 5px 1px 2px;
  border-radius: 2px;
}

.hljs-attr, .hljs-variable {
  color: var(--code-hl-attr-color);
}

.hljs-attribute {
  color: var(--code-hl-attribute-color);
}

.hljs-keyword,
.hljs-meta-keyword,
.hljs-deletion {
  color: var(--code-hl-keyword-color);
}

.hljs-type,
.hljs-string,
.hljs-selector-id,
.hljs-selector-class,
.hljs-quote,
.hljs-template-tag {
  color: var(--code-hl-type-color);
}

.hljs-number {
  color: var(--code-hl-number-color);
}

.hljs-literal {
  color: var(--code-hl-keyword-color);
}

.hljs-emphasis {
  font-style: italic;
}

.hljs-strong {
  font-weight: bold;
}

.hljs-selector-tag,
.hljs-doctag,
.hljs-name {
  color: var(--code-hl-number-color);
}

.hljs-built_in,
.hljs-bullet,
.hljs-code,
.hljs-addition {
  color: var(--code-hl-addition-color);
}

.hljs-section {
  color: var(--code-hl-addition-color);
}
